@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.article-image {
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}

.article-image img {
    width: 100%;
    height: 100%;
}

.article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.article-content p {
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: #fff;
}

.article-content p:last-child {
    cursor: pointer;
}

.article-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}
.brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.brand div {
    flex: 1 1;
    max-width: 150px;
    min-height: 120px;
    margin: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.cta-content p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}

.cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000;
}

.cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.cta-btn button {
    background: #000;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    color: #fff;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .cta {
        flex-direction: column;
    }
    .cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .cta {
        margin: 4rem 2rem;
    }
    
    .cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}
.features-container-feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.features-container-feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.features-container-feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.features-container-feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.features-container-feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.features-container-feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .features-container-feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .features-container-feature-title p {
        font-size: 12px;
        line-height: 20px;
    }

    .features-container-feature {
        margin: 1rem 0;
    }
}
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.navbar-links-logo {
    margin-right: 2rem;
}

.navbar-links-logo img{
    width: 62.56px;
    height: 16.02px;
}

.navbar-links-container {
    display: flex;
    flex-direction: row;
}

.navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar-links-container p,
.navbar-sign p,
.navbar-menu-container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.navbar-sign button, 
.navbar-menu-container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px ;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.navbar-menu svg {
    cursor: pointer;
}

.navbar-menu-container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.navbar-menu-container p {
    margin: 1rem 0;
}

.navbar-menu-container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .navbar-links-container {
        display: none;
    }

    .navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .navbar {
        padding: 2rem;
    }

    .navbar-sign {
        display: none;
    }

    .navbar-menu-container {
        top: 20px;
    }

    .navbar-menu-container-links-sign {
        display: block;
    }
}




.blog {
    display: flex;
    flex-direction: column;
}

.blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.blog-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.blog-container {
    display: flex;
    flex-direction: row;
}

.blog-container-groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.blog-container-groupB {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .blog-container {
        flex-direction: column-reverse;
    }

    .blog-container-groupA {
        margin: 2rem 0;
    }

    .blog-container-groupA .blog-container-article {
        width: 48%; 
    }

    .blog-container-groupA .blog-container-article-image {
        height: 25px; 
    }
}

@media screen and (max-width: 700px) {
    .blog-container-groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .blog-container-groupA .blog-container-article {
        width: 100%;
    }

    .blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}
.features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.features-heading {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.features-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.features-heading p {
    font-family: var(--font-family);
    font-weight: 500px;
    font-size: 16px;
    line-height: 30px;
    color: #FF8A71;
    margin-top: 2rem;
}

.features-container {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .features {
        flex-direction: column;
    }

    .features-heading {
        margin: 0 0 2rem 0;
    }
}

@media screen and (max-width: 550px) {
    .features-heading h1 {
